<div class="max-w-3xl m-auto">
  <h1>Routify</h1>

  <h4>
    Routes for Svelte, automated by your filestructure.
  </h4>

  <h5 class="pt-12">Cool, so what are the benefits?</h5>
  <ul>
    <li>
      <h5>Hit the ground running</h5>
      No need to configure a single route.
    </li>
    <li>
      <h5>SSOT and KISS</h5>
      If
      <span class="code">
        /recipes/cupcakes
      </span>
      doesn't exist in your browser, it doesn't exist in your project. That's
      easy debugging.
    </li>
    <li>
      <h5>Scoped everything</h5>
      Layouts, helpers, props, fallbacks, you name it. It's all scoped and
      context-aware.
    </li>
    <li>
      <h5>Resets</h5>
      Well, sometimes you don't want everything to be scoped. Maybe you don't want
      /restaurants/burger-queen to inherit any layout.
    </li>
    <li>
      <h5>And so on</h5>
      PWA ready, Code-splitting, Realtime route generation.
    </li>
  </ul>

</div>
